
body{
  background-color:rgba(0, 0, 255, 0.1);
  padding:4rem;
}
.container{
  display:grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(6,3.5rem);
  font:bold 1.5rem Helvetica Arial sans-serif;
}
.caption,.temp,.weather_state,.pressure,.wind_speed,.pressure,.nearest_station{
  grid-column-start:2;
  grid-column-end:7;
  line-height: 3.5rem;
  
}
.nearest_station{
  font-size: 1rem;
}
.weather_state img{
  width: 2rem;
  vertical-align: middle;
}
.gif{
  grid-column-start: 8;
  grid-column-end:12;
  grid-row-start: 1;
  grid-row-end: 7;
}
.gif>img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}